import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { MultiCallAria2cRPCRequest } from '../model/RPCRequestModel';
import { RPCResponse, SessionInfo,  Version } from '../model/RPCResponseModel';
import { BaseUrlService } from './base-url.service';

@Injectable({
  providedIn: 'root'
})
export class Aria2ApiService {

  constructor(
    private baseUrlService: BaseUrlService,
    private httpClient: HttpClient,
  ) { }

  public executeMultiCall(multiCallAria2cRPCRequest: MultiCallAria2cRPCRequest): Observable<RPCResponse> {
    const rpcURL = `${this.baseUrlService.getBaseURL()}${environment.RPC_URI}`;
    return this.httpClient.post(rpcURL, multiCallAria2cRPCRequest).pipe(
      map((res) => res as RPCResponse),
      catchError(() => throwError('Error in executing multi-call'))
    );
  }

  public getAria2Version(): Observable<Version> {
    const rpcURL = `${this.baseUrlService.getBaseURL()}${environment.RPC_URI}?showOutput=true`;
    const versionRequestBody = {
      method: environment.ARIA2_VERSION,
      params: [[]]
    }as MultiCallAria2cRPCRequest;
    return this.httpClient.post(rpcURL, versionRequestBody).pipe(
      map((res) => res as Version),
      catchError(() => throwError('Error in fetching version'))
    );
  }

  public getAria2SessionInfo(): Observable<SessionInfo> {
    const rpcURL = `${this.baseUrlService.getBaseURL()}${environment.RPC_URI}?showOutput=true`;
    const sessionInfoRequestBody = {
      method: environment.ARIA2_SESSION_INFO,
      params: [[]]
    }as MultiCallAria2cRPCRequest;
    return this.httpClient.post(rpcURL, sessionInfoRequestBody).pipe(
      map((res) => res as SessionInfo),
      catchError(() => throwError('Error in fetching session info'))
    );
  }
}
