import { Component, OnDestroy, VERSION } from '@angular/core';
import { Subscription } from 'rxjs';
import { Result, Status } from './model/RPCResponseModel';
import { Aria2RpcFeedService } from './service/aria2-rpc-feed.service';

interface DownloadFilter {
  readonly filterName: string;
  readonly shortName: string;
  isChecked: boolean;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy {

  appTitle = 'Aria2cWebUI';
  dFilter: DownloadFilter[] = [];
  results: Result[] = [];
  private rpcFeedSubscription: Subscription;

  constructor(
    private rpcFeedService: Aria2RpcFeedService
  ) {
    /* this.rpcFeedSubscription = this.rpcFeedService.getFeedObservable().subscribe(rpcMessageEvent => {
      if (rpcMessageEvent !== undefined && rpcMessageEvent.data !== undefined) {
        if ((rpcMessageEvent.data.indexOf('system.multicall') !== -1) &&
          (rpcMessageEvent.data.indexOf('result') !== -1)) { // Download status is returned
          const status = JSON.parse(rpcMessageEvent.data) as Status;
          this.updateDownloadResults(status);
        }
      }
    }); */
    this.rpcFeedSubscription = this.rpcFeedService.getRPCFeedObservable().subscribe(rpcMessage => {
      if (rpcMessage !== undefined) {
        console.log('rpcMessage');
        console.log(rpcMessage);
        console.log('rpcMessage.indexOf(\'system.multicall\'): ' + rpcMessage.indexOf('system.multicall'));
        console.log('rpcMessage.indexOf(\'result\') !== -1 : ' + rpcMessage.indexOf('result'));
        console.log('\n\n');
        if ((rpcMessage.indexOf('system.multicall') !== -1) &&
          (rpcMessage.indexOf('result') !== -1)) { // Download status is returned
          const status = JSON.parse(rpcMessage) as Status;
          this.updateDownloadResults(status);
        }
      }
    });
    this.initializeDFilter();
  }

  initializeDFilter(): void {
    this.dFilter.push({ filterName: 'running', shortName: 'Running', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'active', shortName: 'Active', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'waiting', shortName: 'Waiting', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'complete', shortName: 'Complete', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'error', shortName: 'Error', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'paused', shortName: 'Paused', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'removed', shortName: 'Removed', isChecked: true } as DownloadFilter);
    this.dFilter.push({ filterName: 'hideLinkedMetadata', shortName: 'Hide linked meta-data', isChecked: true } as DownloadFilter);
  }

  trackByGID(index: number, item: Result): string {
    return item.gid;
  }

  filterAndSortByGID(unsortedResults?: Result[]): Result[] {
    if (unsortedResults !== undefined) {
      return unsortedResults
        .filter(filter => this.getFilters().indexOf(filter?.status) !== -1)
        .sort((result1, result2) => result1.gid > result2.gid ? -1 : 1);
    }
    return [];
  }

  /**
   * Cleanup or release the resources on exiting
   */
  ngOnDestroy(): void {
    // Unsubscribe from feed
    this.rpcFeedSubscription.unsubscribe();
  }

  private updateDownloadResults(status: Status): void {
    if (status.statusCode === 200) {
      if (status.result !== undefined) {
        this.results = [].concat.apply([], status.result);
      }
    } else {
      console.error(status.error);
    }
  }

  resetFilter(): void {
    this.dFilter.forEach(filter => filter.isChecked = true);
  }

  toggleFilter(): void {
    this.dFilter.forEach(filter => filter.isChecked = !filter.isChecked);
  }

  getFilters(): string[] {
    return this.dFilter.filter(filter => filter.isChecked).map(filter => filter.filterName);
  }
}
