import {Aria2ApiService} from '../service/aria2-api.service';
import {SnackbarService} from '../service/snackbar.service';
import {buildMultiCallRequest, isValidURIs, toDownloadURIArray} from './RequestUtil';

export abstract class URIValidation {

  private apiService: Aria2ApiService;
  private alertService: SnackbarService;

  protected constructor(
    aria2ApiService,
    snackbarService
  ) {
    this.apiService = aria2ApiService;
    this.alertService = snackbarService;
  }

  makeAddURIRequest(dURIs: string): void {
    const downloadURIs: string[] = toDownloadURIArray(dURIs);
    if (isValidURIs(downloadURIs)) {
      this.apiService.executeMultiCall(buildMultiCallRequest(downloadURIs)).subscribe(_ => {
        this.alertService.showSuccessSnackbar('URIs added successfully');
      }, err => {
        this.alertService.showErrorSnackbar('Error in executing multi-call add URI request');
        console.error(err);
      });
    } else {
      this.alertService.showErrorSnackbar('Error occurred in parsing the download URI');
    }
  }

}
