import { Component, Input, OnInit } from '@angular/core';
import { Result } from 'src/app/model/RPCResponseModel';
import { baseName, formatBytes, formatCompletion, formatSeconds } from 'src/app/util/FormatUtils';

@Component({
  selector: 'app-download-item',
  templateUrl: './download-item.component.html',
  styleUrls: ['./download-item.component.scss']
})
export class DownloadItemComponent implements OnInit {

  @Input() result: Result;

  isExpanded = false;

  constructor() { }

  ngOnInit(): void {
  }

  toggleExpandableSection(): void {
    this.isExpanded = !this.isExpanded;
  }

  baseName(path: string): string {
    return baseName(path);
  }

  formatBytes(bytes, decimals: number = 2): string {
    const formattedBytes = formatBytes(bytes, decimals);
    if (formattedBytes.indexOf('undefined') !== -1 || formattedBytes.indexOf('NaN') !== -1) {
      return '0 B';
    }

    return formattedBytes;
  }

  formatCompletion(completedLength: string, totalLength: string, isRemaining: boolean = false): number {
    return this.toNumber(formatCompletion(completedLength, totalLength, isRemaining));
  }

  formatSeconds(completedLength: string, totalLength: string, downloadSpeed: string): string {
    return formatSeconds(completedLength, totalLength, downloadSpeed);
  }

  getStatusIcon(status: string): string {
    switch (status) {
      case 'active': return 'play_circle_outline';
      case 'waiting': return 'av_timer';
      case 'paused': return 'pause_circle_outline';
      case 'error': return 'error_outline';
      case 'complete': return 'check_circle_outline';
      case 'removed': return 'remove_circle_outline';
    }
  }
  toNumber(str: string): number {
    return parseFloat(str);
  }
}
