import { Injectable } from '@angular/core';
import { MatSnackBar, MatSnackBarConfig, MatSnackBarHorizontalPosition,  MatSnackBarRef, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { environment } from 'src/environments/environment';
import { SnackBarConfig } from '../model/SnackbarModel';

@Injectable({
  providedIn: 'root'
})
export class SnackbarService {

  snackBarConfig: MatSnackBarConfig;
  snackBarRef: MatSnackBarRef<any>;
  horizontalPosition: MatSnackBarHorizontalPosition = 'center';
  verticalPosition: MatSnackBarVerticalPosition = 'bottom';
  snackBarAutoHideDuration = 1500;
  defaultPanel = 'glam-snackbar';

  constructor(
    private snackBar: MatSnackBar
  ) { }

  openSnackBar(message: string, options: SnackBarConfig = {} as SnackBarConfig): void {
    this.snackBarConfig = new MatSnackBarConfig();
    this.snackBarConfig.horizontalPosition = options.horizontalPosition || this.horizontalPosition;
    this.snackBarConfig.verticalPosition = options.verticalPosition || this.verticalPosition;
    this.snackBarConfig.duration = options.duration || this.snackBarAutoHideDuration;
    this.snackBarConfig.panelClass = options.panelClass || this.defaultPanel;
    this.snackBarRef = this.snackBar.open(message, '', this.snackBarConfig);
  }

  showSuccessSnackbar(message: string): void {
    this.openSnackBar(message, { panelClass: environment.PANEL_CLASS_G } as SnackBarConfig);
  }

  showErrorSnackbar(message: string): void {
    this.openSnackBar(message, { panelClass: environment.PANEL_CLASS_R } as SnackBarConfig);
  }
}
