import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Aria2ApiService } from '../../service/aria2-api.service';
import { SnackbarService } from '../../service/snackbar.service';
import { URIValidation } from '../../util/URIValidation';

@Component({
  selector: 'app-upload-file',
  templateUrl: './upload-file.component.html',
  styleUrls: ['./upload-file.component.scss']
})
export class UploadFileComponent extends URIValidation implements OnInit {

  selectedFileName = 'Choose file';
  fileList: FileList;

  constructor(
    private aria2ApiService: Aria2ApiService,
    private snackbarService: SnackbarService,
    private matDialogRef: MatDialogRef<UploadFileComponent>
  ) {
    super(aria2ApiService, snackbarService);
  }

  ngOnInit(): void {
  }

  fileUploadChangeEvent(event: Event): void {
    const fileInput: HTMLInputElement = event.target as HTMLInputElement;
    if(fileInput.files !== undefined && fileInput.files.length > 0) {
      this.fileList = fileInput.files;
      this.selectedFileName = this.fileList.item(0)?.name;
    }
  }

  uploadFile(): void {
    if(this.fileList?.length > 0) {
      //TODO: Handle multiple file pick
      const fileReader = new FileReader();
      const file = this.fileList?.item(0);
      this.selectedFileName = file?.name;
      fileReader.onload = e => {
        const URIs = e.target.result as string;
        this.makeAddURIRequest(URIs);
      };
      fileReader.onerror = e => {
        this.snackbarService.showErrorSnackbar('Error occurred in reading text file');
        console.error(e);
      };
      fileReader.readAsText(file, 'UTF-8');
      this.matDialogRef.close();
    }
  }

  closeDialog(): void {
    this.matDialogRef.close();
  }
}
