// import { SocketService } from './socket.service';
import { NgZone } from '@angular/core';
import { Injectable } from '@angular/core';
import { Observable, Observer } from 'rxjs';
import { environment } from '../../environments/environment';
import { Socket } from '../model/SocketModel';
import * as socketIO from 'socket.io-client';
import { BaseUrlService } from './base-url.service';
// import { Socket } from 'ngx-socket-io';

@Injectable({
  providedIn: 'root'
})
export class Aria2RpcFeedService {

  private readonly observable: Observable<MessageEvent>;
  private readonly rpcObservable: Observable<any>;

  constructor(
    private ngZone: NgZone,
    private baseUrlService: BaseUrlService,
    // private socketService: SocketService
  ) {
    if (this.rpcObservable === undefined) {
      this.rpcObservable = new Observable((observer: Observer<any>) => {

        const socket = socketIO('http://localhost:3000');

        socket.on('connect', (message) => {
          console.log('Connected to server successfully');
          console.debug(message);
          socket.on('testPong', (pongMessage) => {
            console.debug(pongMessage);
            setTimeout(() => {
              this.emitPingMessage(socket);
            }, 3000);
          });
          socket.on('rpc-message', (rpcMessage) => {
            this.ngZone.run(() => {
              console.log(rpcMessage);
              observer.next(rpcMessage);
            });
          });
          this.emitPingMessage(socket);
        });

        socket.on('disconnect', () => {
          console.error('dis-connected from server. Refresh to reconnect');
        });

      });

      console.debug('RPC Feed observable is initialized');
    } else {
      console.debug('RPC Feed observable is already initialized');
    }
    /* if(this.observable === undefined) {
      this.observable = new Observable((observer: Observer<MessageEvent>) => {

        const eventSource = this.getEventSource();

        eventSource.onmessage = event => {
          this.ngZone.run(() => {
            observer.next(event);
          });
        };

        eventSource.onerror = error => {
          this.ngZone.run(() => {
            console.error('Error in feed observable');
            console.error(error);
            observer.error(error);
          });
        }
      });

      console.debug('RPC Feed observable is initialized');
    } else {
      console.debug('RPC Feed observable is already initialized');
    } */
  }

  /**
   * Returns the feed observable object
   *
   */
  public getFeedObservable = (): Observable<MessageEvent> => this.observable;

  /**
   * Returns the event-source for RPC feed URI
   *
   */
  private getEventSource = (): EventSource => new EventSource(environment.RPC_FEED_URI);

  public getRPCFeedObservable = (): Observable<any> => this.rpcObservable;

  private emitPingMessage(socket: Socket): void {
    socket.emit('testPing', {
        type: 'keep-alive',
        message: 'ping',
        timestamp: new Date().toISOString()
    });
}

}
